//L'algorithme de brassage de Durstenfeld, une version optimise par ordinateur de l'algorithme de Fisher-Yates.
//L'algorithme fonctionne en choisissant un lment alatoire pour chaque lment du tableau original, puis en l'excluant du prochain tirage. Il change l'lment choisi avec l'lment actuel, puis choisit l'lment alatoire suivant dans le reste.
//Note : La fonction shuffle n'est pas une fonction pure, elle modifie le tableau original.
//afin d'viter la duplication de la mmoire
export const shuffle = (array: any) => {
  var currentIndex = array.length, temporaryValue, randomIndex;

  // Alors qu'il reste des lments  mlanger...
  while (0 !== currentIndex) {

    // Choisis un lment restant...
    randomIndex = Math.floor(Math.random() * currentIndex);
    currentIndex -= 1;

    // Et le remplacer par l'lment actuel.
    temporaryValue = array[currentIndex];
    array[currentIndex] = array[randomIndex];
    array[randomIndex] = temporaryValue;
  }

  return array.length;
}